import React, {useEffect, useState, useCallback} from 'react';
import {useDropzone} from 'react-dropzone';
import {styled} from 'baseui';
import {UploadIcon} from 'assets/icons/UploadIcon';
import {Col, Row} from "../FlexBox/FlexBox";
import {Plus} from "../../assets/icons/Plus";
import Button from "../Button/Button";

const Text = styled('span', ({$theme}) => ({
    ...$theme.typography.font14,
    fontFamily: $theme.typography.primaryFontFamily,
    color: $theme.colors.textDark,
    marginTop: '15px',
    textAlign: 'center',
}));

const TextHighlighted = styled('span', ({$theme}) => ({
    color: $theme.colors.primary,
    fontWeight: 'bold',
}));

const Container = styled('div', ({props}) => ({
    flex: '1',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '30px',
    borderWidth: '2px',
    borderRadius: '2px',
    borderColor: '#E6E6E6',
    borderStyle: 'dashed',
    backgroundColor: '#ffffff',
    color: '#bdbdbd',
    outline: 'none',
    transition: 'border 0.24s ease-in-out',
    cursor: 'pointer',
}));

const ThumbsContainer = styled('aside', () => ({
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
    marginTop: '16px',
}));

const Thumb = styled('div', ({$theme}) => ({
    ...$theme.borders.borderEA,
    display: 'inline-flex',
    borderRadius: '2px',
    marginBottom: '8px',
    marginRight: '8px',
    width: '100px',
    height: '100px',
    padding: '4px',
    boxSizing: 'border-box',
}));

const thumbInner = {
    display: 'flex',
    minWidth: 0,
    overflow: 'hidden',
};

const img = {
    display: 'block',
    width: 'auto',
    height: '100%',
};

function Uploader({onChange, imageURL}: any) {
    const [files, setFiles] = useState(
        imageURL ? [{name: 'demo', preview: imageURL}] : []
    );
    const {getRootProps, getInputProps} = useDropzone({
        accept: 'image/png,image/jpg,image/jpeg,image/gif,video/ogg,video/webm,video/mp4',
        multiple: false,
        onDrop: useCallback(
            (acceptedFiles) => {
                setFiles(
                    acceptedFiles.map((file) =>
                        Object.assign(file, {
                            preview: URL.createObjectURL(file),
                        })
                    )
                );
                onChange(acceptedFiles);
            },
            [onChange]
        ),
    });

    const thumbs = files.map((file) => (
        <Thumb key={file.name}>
            <div style={thumbInner}>
                <img src={file.preview} style={img} alt={file.name}/>
            </div>
        </Thumb>
    ));

    useEffect(
        () => () => {
            // Make sure to revoke the data uris to avoid memory leaks
            files.forEach((file) => URL.revokeObjectURL(file.preview));
        },
        [files]
    );

    return (
        <section className="container uploader">

            <Row>
                <Col md={thumbs.length ? 10 : 12}>
                    <Container {...getRootProps()}>
                        <input {...getInputProps()} />
                        <UploadIcon/>
                        <Text>
                            <TextHighlighted>Drag/Choose</TextHighlighted> your image
                        </Text>
                    </Container>
                </Col>
                <Col md={thumbs.length ? 2 : ''}>
                    <ThumbsContainer>{thumbs}</ThumbsContainer>
                </Col>
            </Row>
        </section>
    );
}

export default Uploader;
